@import url('https://fonts.googleapis.com/css?family=Open+Sans&display=swap');

$linkBlue: #3498db;
$linkPurple: rgb(98, 16, 250);

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  margin: 0px;
  padding: 0px;
  min-height: 100vh;
}

a {
  position: relative;
  text-decoration: none;
}

a::before {
  content: "";
  position: absolute;            
  height: 3px;
  right: 0px;
  bottom: -3px;
  left: 0px;
  background-color: $linkBlue;
}

nav > ul
{
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

.grid > ul
{
  display: grid;
}

.grid > ul
{

  grid-template-columns: repeat(3, 100px);
  grip-gap: 10px;
  justify-content: center;
  justify-items: center;

  @media (min-width: 600px){
    grid-template-columns: repeat(4, 100px);
}
}

.grid > ul > li > a 
{
  font-size: large;
}

nav {
  padding: 0px;

  @media (min-width: 600px){
      padding: 20px;
  }
}


*, *::before, *::after {
  box-sizing: inherit;
}

.App {
  text-align: center;
  background-color: #f7f7f7;
}

.Portfolio{
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

input, .champs select{
  width:50%; 
  font-size:35px; 
  padding:1em 10%;
  clear:both; 
  text-align:right;
}

input:focus
{
  border: blue 10px;
}

label{background:black; padding:3px 6px; font-size:40px; margin-left:1px; font-weight:normal; clear:both;}